body {
  font: 30px "Courier New", Futura, sans-serif;
  margin: 5px; }

.square {
  background: #fff;
  border: 0;
  float: left;
  font-size: 30px;
  font-weight: bold;
  height: 40px;
  margin: 0;
  padding: 0;
  text-align: center;
  width: 40px; }
  .square.unselected {
    padding: 2px;
    background-color: white;
    border-left: darkgray 1px solid;
    border-top: darkgray 1px solid;
    border-right: darkgray 1px solid;
    border-bottom: darkgray 1px solid; }
  .square.selected {
    background-color: lightgrey;
    border: white 1px solid; }
  .square.blowup {
    background-color: red; }
  .square.posited {
    background-color: green; }
  .square.type-0 {
    background-color: lightgray;
    color: gray; }
  .square.type-1 {
    background-color: lightgray;
    color: blue; }
  .square.type-2 {
    background-color: lightgray;
    color: green; }
  .square.type-3 {
    background-color: lightgray;
    color: red; }
  .square.type-4 {
    background-color: lightgray;
    color: darkblue; }
  .square.type-5 {
    background-color: lightgray;
    color: darkgreen; }
  .square.type-6 {
    background-color: lightgray;
    color: darkred; }
  .square.type-7 {
    background-color: lightgray;
    color: darkred; }
  .square.type-8 {
    background-color: lightgray;
    color: darkred; }

.square:focus {
  outline: none; }

.kbd-navigation .square:focus {
  background: #ddd; }

.game {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 0;
  padding: 0; }

.game-info {
  margin-left: 20px; }

.game-board {
  display: grid;
  border: darkgrey 1px solid;
  width: 640px;
  grid-template-columns: 40px 40px 40px 40px 40px 40px 40px 40px 40px 40px 40px 40px 40px 40px 40px 40px; }

.timer {
  color: red; }

.fa-frown-o {
  background: red; }

.fa-smile-o {
  background: yellow; }

.fa-meh-o {
  background: white; }

p {
  margin: 0;
  border: none;
  padding: 0;
  font-size: small; }

.nav {
  display: table;
  width: 100%;
  margin: 0;
  border: none;
  padding: 0;
  background-color: lightgrey; }
  .nav * {
    display: table-cell; }

.new-game {
  font-size: medium; }
  .new-game.hi {
    background: green; }

.toolbar {
  display: table;
  width: 640px; }
  .toolbar * {
    display: table-cell; }



